html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  font-family: Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  font-size: 16px;
  background-color: #131419;
  color: #fff;
}

#root {
  min-height: 100%;
  height: 100%;
  padding-bottom: 10px;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
  font-family: inherit;
}
